import React from 'react'
import { Link } from 'gatsby'
import links from '../constants/links'
import { TbAlignRight } from 'react-icons/tb'

const Nav = ({toggleSidebar}) => {
    return (
        <nav className="flex justify-start items-center" aria-label="Global">
            <div className="flex lg:hidden">
                <button
                    type="button"
                    className="-m-2.5 inline-flex items-center justify-center rounded-md p-2.5 text-gray-700"
                    onClick={toggleSidebar}
                >
                    <span className="sr-only">Open main menu</span>
                    <TbAlignRight className="h-8 w-8" aria-hidden="true" />
                </button>
            </div>
            <div className="hidden mr-8 lg:flex lg:gap-x-16">
                {links.map((item) => (
                    <Link key={item.id} to={item.url} className="text-sm font-semibold leading-10 text-gray-500 hover:text-gray-900 transition-all" activeClassName='text-sm font-semibold leading-10 text-gray-900 after:block after:h-0.5 after:bg-gray-900'>
                    {item.text}
                    </Link>
                ))}
            </div>
        </nav>
    )
}

export default Nav
