import React from 'react'
import { Link } from 'gatsby'
import links from '../constants/links'
import { TbX } from 'react-icons/tb'

const Sidebar = ({isOpen, toggleSidebar}) => {
  return (
    <aside className={`fixed inset-0 overflow-y-auto bg-white px-4 lg:px-8 py-4 lg:hidden transition ease-in-out ${isOpen ? 'opacity-100 z-50' : 'opacity-0 -z-50'}`}>
        <div className="flex items-center justify-between">
            <Link to='/' onClick={toggleSidebar}>
                <p className='text-2xl tracking-wider font-extrabold text-gray-900 sm:text-2xl'>Liang<br/>
                Lingjiang</p>
            </Link>
            <p className={'fixed text-xs tracking-wider font-light text-gray-900 mt-20 pt-3 sm:text-xs -m-1.5 p-1.5'}>UX Designer / Front-end Developer</p>
            <button
                type="button"
                className="-m-2.5 rounded-md p-2.5 text-gray-700"
                onClick={toggleSidebar}
            >
                <span className="sr-only">Close menu</span>
                <TbX className="h-8 w-8 -mt-4" aria-hidden="true" />
            </button>
        </div>
        <div className="mt-20 flow-root">
            <div className="-my-6 divide-y divide-gray-500/10">
                <div className="space-y-2 py-6">
                {links.map((item) => (
                    <Link
                    key={item.id}
                    to={item.url}
                    className="text-4xl -mx-3 block rounded-lg leading-loose px-3 font-semibold text-gray-900 hover:bg-gray-400/10"
                    activeClassName='after:block after:h-0.5 after:bg-gray-900'
                    onClick={toggleSidebar}
                    >
                    {item.text}
                    </Link>
                ))}
                </div>
            </div>
        </div>
    </aside>
  )
}

export default Sidebar
