import React, { useState, useEffect } from 'react';
import { useStaticQuery, graphql } from "gatsby"
import '../assets/css/global.css'

import Header from "./header"
import Footer from "./footer"
import Sidebar from './Sidebar'

function useScrollDirection() {
  const [scrollDirection, setScrollDirection] = useState(null);

  useEffect(() => {
    let lastScrollY = window.pageYOffset;

    const updateScrollDirection = () => {
      const scrollY = window.pageYOffset;
      const direction = scrollY > lastScrollY ? "down" : "up";
      if (direction !== scrollDirection && (scrollY - lastScrollY > 10 || scrollY - lastScrollY < -10)) {
        setScrollDirection(direction);
      }
      lastScrollY = scrollY > 0 ? scrollY : 0;
    };
    window.addEventListener("scroll", updateScrollDirection); // add event listener
    return () => {
      window.removeEventListener("scroll", updateScrollDirection); // clean up
    }
  }, [scrollDirection]);

  return scrollDirection;
};

const Layout = ({ children }) => {
  // const [top, setTop] = useState(true)
  const [isOpen, setIsOpen] = useState(false)
  const scrollDirection = useScrollDirection()

  const toggleSidebar = () => {
    setIsOpen(!isOpen)
  }

  // useEffect(() => {
  //   const scrollHandler = () => {
  //     window.pageYOffset > 10 ? setTop(false) : setTop(true)
  //   };
  //   window.addEventListener('scroll', scrollHandler)
  //   return () => window.removeEventListener('scroll', scrollHandler)
  // }, [top])

  useEffect(() => {
    const handleContextmenu = e => {
        e.preventDefault()
    }
    document.addEventListener('contextmenu', handleContextmenu)
    return function cleanup() {
        document.removeEventListener('contextmenu', handleContextmenu)
    }
  }, [ ])

  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.site.siteMetadata?.title || `Title`} toggleSidebar={toggleSidebar} scrollDirection={scrollDirection} />
      <Sidebar isOpen={isOpen} toggleSidebar={toggleSidebar} />
      <main className='pt-4 leading-relaxed mt-12 md:mt-16 lg:mt-28'>
        {/* <p className={`fixed text-xs tracking-wider font-light text-gray-900 mt-16 px-8 sm:text-xs lg:pl-8 p-1.5 ease-linear duration-200 ${!top && 'opacity-0'}`}>Liang Lingjiang / UX Designer / Front-end Developer</p> */}
        {children}
      </main>
      <Footer />
    </>
  )
}

export default Layout
